import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations.js"
import actions from "./actions.js"


//全局组成
Vue.use(Vuex);

// 创建store对象
const store=new Vuex.Store({
  //状态数据
  state:{
    // 主题分类数据
     topics:[
        {name:"all",desc:"全部"},
        {name:"good",desc:"精华"},
        {name:"share",desc:"分享"},
        {name:"ask",desc:"问答"},
        {name:"job",desc:"招聘"},
        {name:"dev",desc:"测试"}
     ],
    isLogined: false, //表示登录状态
    loginname:'',// 登录用户名
    accesstoken:'',//登录用户的accesstoken
    author_id:'',//登录用户的id

  },
  // 状态数据的计算属性
  //状态数据计算属性
  	getters: {
  		moreMenus: function(state) {
  			if(state.isLogined) {
  				return state.LoginedmoreMenus;
  			}
  			return state.unLoginedmoreMenus;
      },
      getTopicDescByTab:function(state){
	  		return function(tab){
	  		for(var index=0;index < state.topics.length;index++){
	  			if(state.topics[index].name == tab){
	  				return state.topics[index].desc;
	  				}
	  			}	  			
	  		};
	  	}
  	},
  	mutations: mutations,
  	actions

});


export default store;



