<template>
  <div v-if="topic">
    <el-container>
      <el-header height="140px">
        <h2>{{topic.title}}</h2>
        <div>
          <span>
            作者:
            <el-link :href="'#/user/'+topic.author.loginname" type="info">{{topic.author.loginname}}</el-link>
          </span>
          <span>{{topic.visit_count}}次浏览</span>
          <span>来自: {{getTopicDescByTab(topic.tab)}}</span>
        </div>
        <el-button v-if="topic.is_collect == false" type="success" @click.native="collect">收藏</el-button>
        <el-button v-else @click.native="decollect" type="info" plain>取消收藏</el-button>
        <el-link
          v-if="topic.author_id == $store.state.author_id"
          type="info"
          :href="'#/topic/'+topic.id+'/edit'"
        >编辑</el-link>
      </el-header>
      <el-main v-html="topic.content"></el-main>

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>评论</span>
        </div>
        <div v-for="(item,index) in topic.replies" :key="index" class="text item">
          <el-avatar  shape="square" :size="40"  :src="item.author.avatar_url"></el-avatar>
            
          <el-link type="primary" :href="'#/user/'+item.author.loginname">{{item.author.loginname}}</el-link><el-tag>{{index+1}}楼</el-tag>
          
          <div style="padding: 5px;" class="" v-html="item.content"></div>
          
          <div style="float: right;margin-top:-80px;"><i class="el-icon-star-off"></i>{{item.ups.length}}</div>
           <el-divider></el-divider>
        </div>
      </el-card>
      <!-- <el-tag type="warning">评论</el-tag>
              <el-row v-for="(item,index) in topic.replies" :key="index">
                <el-col :span="2">
                  <div class="grid-content bg-purple">
                      <el-avatar shape="square" :size="40"  :src="item.author.avatar_url"></el-avatar>
                  </div>
                </el-col>
                <el-col :span="22" style="height:40px;">
                  <div class="grid-content bg-purple-light">
                      <el-link type="primary" :href="'#/user/'+item.author.loginname">{{item.author.loginname}}</el-link> 
                      <el-tag>{{index+1}}楼</el-tag>
                  </div>
                </el-col>
                

                <el-col :span="16"><div v-html="item.content" class="grid-content bg-purple-light"></div></el-col>
                <el-col :span="6"><div class="grid-content bg-purple-light"><i class="el-icon-star-off"></i>{{item.ups.length}}</div></el-col>
      </el-row>-->
      <el-footer style="height: 200px;">
        <el-form ref="form" label-width="80px">
          <el-form-item label="回复评论">
            <el-input type="textarea" v-model="replyContent"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.native="addReply(1)">回复</el-button>
          </el-form-item>
        </el-form>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  name: "topic",
  props: ["id"],
  data() {
    return {
      topic: null,
      replyContent: ""
    };
  },
  computed: {
    ...mapState(["isLogined"]),
    ...mapGetters(["getTopicDescByTab"])
  },
  methods: {
    //回复评论
    addReply: function(replyTab) {
      //若replyTab为1表示为主话题进行评论
      //若replyTab为2表示为回复其他评论
      this.$http
        .post("/topic/" + this.topic.id + "replies", {
          accesstoken: this.$store.state.accesstoken,
          content: this.replyContent
        })
        .then(function(res) {
          //返回结果 {success:true,reply_id:""}
        });
    },
    // 收藏主题
    collect: function() {
      if (this.$store.state.isLogined == false) {
        this.$message({
          message: "未登录,请先登录",
          type: "warning"
        });
        return;
      }
      // console.log("ok")
      var that = this;
      this.$http
        .post("/topic_collect/collect", {
          accesstoken: this.$store.state.accesstoken,
          topic_id: this.topic.id
        })
        .then(function(res) {
          console.log(res.data);
          // 判断是否成功
          if (res.data && res.data.success) {
            // ajax修改按钮状态 收藏--取消收藏
            that.topic.is_collect = true;
          }
        });
    },
    // 取消收藏
    decollect: function() {
      var that = this;
      this.$http
        .post("/topic_collect/de_collect", {
          accesstoken: this.$store.state.accesstoken,
          topic_id: this.topic.id
        })
        .then(function(res) {
          console.log(res.data);
          // 判断是否成功
          if (res.data && res.data.success) {
            // ajax修改按钮状态 取消收藏--收藏
            that.topic.is_collect = false;
          }
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http
        .get("/topic/" + to.params.id, {
          params: {
            accesstoken: vm.$store.state.accesstoken
          }
        })
        .then(function(res) {
          console.log(res.data.data);
          vm.topic = res.data.data;
        });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    this.$http
      .get("/topic/" + to.params.id, {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(res) {
        console.log(res.data.data);
        that.topic = res.data.data;
      });
  }
};
</script>

<style scoped>
header {
  /* border-radius: 5px; */
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: #fff;
}
main {
  margin-top: 1px;
  background-color: #fff;
  margin-bottom: 1px;
}
.markdown-text img {
  /* height: auto; */
  width: 500px;
  /* vertical-align: middle; */
}
.el-row {
  background-color: #fff;
}
.el-footer {
  margin-top: 1px;
  
  background-color: #fff;
}
.el-form {
  margin-top: 5px;
}
</style>


