<template>
<div>
  <el-tabs v-model="activename" @tab-click="handleClick"  type="border-card" >
    <el-tab-pane v-for="(item,index) in topics" :name="item.name" :key="index" :label="item.desc" >
      <!-- {{lists}} --> 
    </el-tab-pane>
  </el-tabs>
  <el-table
        :data="lists"
        style="width: 100%"
        @row-click="toplic"
      >
        <el-table-column width="80">
          <span slot-scope="scope">
            <el-avatar
              shape="square"
              :size="40"
              :src="scope.row.author.avatar_url"
              @error="errorHandler"
            ></el-avatar>
          </span>
        </el-table-column>

        <el-table-column prop="visit_count" width="100">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>阅读数: {{ scope.row.visit_count}}</p>
              <p>回复数: {{ scope.row.reply_count }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{scope.row.visit_count}} / {{ scope.row.reply_count}}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>

        <el-table-column
          v-if="$route.path=='/topiclist/all'||$route.path=='/topiclist/good' "
          width="80"
        >
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.top" size="medium">置顶</el-tag>
            <el-tag type="success" v-else-if="scope.row.good" size="medium">精华</el-tag>
            <el-tag
              type="success"
              v-else-if="$route.path=='/topiclist/all'||$route.path=='/topiclist/good' "
              size="medium"
            >{{getTopicDescByTab(scope.row.tab)}}</el-tag>
          </template>
        </el-table-column>

        <el-table-column v-else width="80">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.top" size="medium">置顶</el-tag>
            <el-tag type="success" v-else-if="scope.row.good" size="medium">精华</el-tag>
          </template>
        </el-table-column>

        <el-table-column prop="title" width="1200"></el-table-column>
      </el-table>
      <nav aria-label="...">
  <ul class="pager" >
    <li :class="page == 1 ? 'previous disabled' : 'previous' "  @click.stop.prevent="sy($event)"><a  href="#"><span aria-hidden="true">&larr;</span> 上一页</a></li>
    <li class="next" @click.stop.prevent="xy"><a href="#">下一页<span aria-hidden="true">&rarr;</span></a></li>
  </ul>
</nav>
      </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  name: "topiclist",
  props: ["name"],
  data() {
    return {
      
      loading: false,
      activename: "all",
      lists: [],
      page: 1, //当前页数
      limit: 15 //每一页显示的主题数量
    };
  },
  computed: {
    ...mapState(["topics"]),
    ...mapGetters(["getTopicDescByTab"]),
  },
  methods: {
    // 上一页
    sy(event){
        if(this.page == 1){
          this.$alert('当前已经是第一页了哦',{
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'success',
              message: '请你不要再点击我了~'
            });
          }
        });
        }else{
          var that=this;
        this.page=this.page-1,
        this.$http.get('/topics',{
          params:{
            tab:this.name,
            page:this.page,
            limit:this.limit
          }
        }).then(function(response){
          console.log(response.data.data)
          that.lists = response.data.data;
        });
        }
    },
    // 下一页
    xy(){
      var that=this;
        this.page=this.page+1,
        this.$http.get('/topics',{
          params:{
            tab:this.name,
            page:this.page,
            limit:this.limit
          }
        }).then(function(response){
          console.log(response.data.data)
          that.lists = response.data.data;
        });
    },
    // 向主题详情跳转
    toplic(row, column, event){
        // console.log(row)
        // console.log(column)
        // console.log(event)
        // console.log("ok")
        //实现编程式路由跳转
      this.$router.push("/topic/"+row.id);
    },
    // 图片加载错误
    errorHandler() {
      return true;
    },
    // 切换主题分类
    handleClick(tab, event) {
      // console.log(tab);
      // console.log(event);
      // console.log(tab.$route.path)
      console.log(tab.name);
      //实现编程式路由跳转
      this.$router.push("/topiclist/" + tab.name);
    }
  },
  // 2.可以使用导航守卫回调函数 （更适合动态路由）
  beforeRouteEnter(to, from, next) {
    // 进入组件之前的回调操作
    // to表示待进入的路由组件
    // from表示离开的路由组件
    // next执行下一步操作
    // this.$http.get("/topics")//此处不能使用this
    next(function(vm) {
      // 表示当前组件的实例对象
      vm.$http
        .get("/topics", {
          params: {
            tab: to.params.name,
            page: vm.page,
            limit: vm.limit
          }
        })
        .then(function(response) {
          console.log(response.data.data);
          if (response.data.success) {
            vm.lists = response.data.data;
          }
        });
    });
  },
  beforeRouteUpdate: function(to, from, next) {
    var that = this;
    this.page = 1;
    this.$http
      .get("/topics", {
        params: {
          tab: to.params.name,
          page: this.page,
          limit: this.limit
        }
      })
      .then(function(response) {
        console.log(response.data.data);
        if (response.data.success) {
          that.lists = response.data.data;
        }
      });
    // 执行下一步操作
    next();
  }
};
</script>

<style>
</style>


