<template>
  <div>
    <el-select v-model="value">
      <el-option v-for="item in list" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <el-input type="textarea" autosize v-model="title" placeholder="标题"></el-input>
    <mavon-editor v-model="content" :toolbars="toolbars" @imgAdd="uploadImg"></mavon-editor>
    <div>
      <el-button type="success" @click.native="addContent">发表话题</el-button>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "create",
  components: {
    mavonEditor
  },
  data() {
    return {
      title: "",
      content: "",
      value: "请选择",
      list: ["请选择", "测试", "分享", "问答", "招聘"],
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        //   strikethrough: true, // 中划线
        mark: true, // 标记
        //   superscript: true, // 上角标
        //   subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        //   fullscreen: true, // 全屏编辑
        //   readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        //   navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    };
  },
  methods: {
    // 上传本地图片
    // pos表示当前markdown图片序号
    //file表示上传图片的file对象
    uploadImg: function(pos, file) {
      console.log(pos);
      console.log(file);
      var params = new FromData();
      params.append("smfile", file);
      // 第一步：先把图片上传到云服务器（axios发送请求） https://sm.ms/api/v2/upload
      axios
        .post("https://sm.ms/api/v2/upload", params, {
          // 请求配置信息(上传文件需要给出附加配置信息)
          // 文件上传必须是post请求
          // 文件上传必须指定content-type Content-Type:multipart/form-data
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(function(res) {
          // 第二部：图片上传成功后 修改markdown的内容
          console.log(res);
          this.$refs.md.$img2Url(pos, res.data.data.url);
        });
    },
    addContent: function() {
      this.$http
        .post("topics", {
          title: this.title,
          content: this.content,
          tab: "dev",
          accesstoken: this.$store.state.accesstoken
        })
        .then(function() {
          if (res.data.seccess == true) {
            that.$router.replace("/topic/" + res.data.topic_id);
          }
        });
    }
  }
};
</script>

<style scoped>
.el-select{
    margin-bottom: 10px;
}
.el-button{
    margin-top: 20px;
    width: 100%;
}

</style>


