<template>
  <div>
    <!-- 个人中心 center -->
  </div>
</template>

<script>
  export default{
    name:"center",
    computed:{
			loginname:function(){
				return this.$store.state.loginname;
			}
		},
		created(){
      // 路由跳转
			this.$router.replace('/user/'+this.loginname)
		},
  }
</script>

