<template>
  <div>
    <!-- 退出 logout -->
    <el-alert v-if="isShowMsg" title="你还没有登录哦,请先登录哦" type="error"><el-link type="primary" href="#/login">登录链接</el-link></el-alert>
    
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "logout",
  data() {
    return {
      isShowMsg: false
    };
  },
  created() {
    // 如果用户未登录  不应该访问退出操作
    if (this.$store.state.isLogined == false) {
      this.isShowMsg = true;
      return;
    }
    //实现退出操作
    var that = this;
    this.$confirm("此操作将退出, 是否继续?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning",
      // center: true
    })
      .then(() => {
        //1.清空本地数据
        window.localStorage.clear();
        // 2.恢复vuex中的数据为原始值
        this.$store.commit("mutationLogout");
        this.$router.push("/");
        this.$router.go(0);
        that.$message({
          type: "success",
          message: "退出成功!"
        });
        
      })
      .catch(() => {
        that.$router.push("/");
        this.$message({
          type: "info",
          message: "已取消退出"
        });
      });
  }
};
</script>

<style>
</style>



