<template>
  <div>
    <el-select v-model="value" >
      <el-option v-for="item in list" :key="item.value" :label="item" :value="item"></el-option>
    </el-select>
    <el-input type="textarea" autosize v-model="topic.title"></el-input>
    <mavon-editor v-model="topic.content" :toolbars="toolbars"></mavon-editor>
    <div>
        <el-button type="success" @click.native="edittopic">修改话题</el-button>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "edit",
  props: ["id"],
  data() {
    return {
      topic: {
        title: "",
        content: ""
      },
      value: "测试",
      list: ["请选择", "分享", "问答", "招聘", "客户端测试"],
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        //   strikethrough: true, // 中划线
        mark: true, // 标记
        //   superscript: true, // 上角标
        //   subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        //   fullscreen: true, // 全屏编辑
        //   readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        //   navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    };
  },
  components: {
    mavonEditor
  },
  methods: {
    edittopic: function() {
      var that = this;
      this.$http
        .post("/topic/update", {
          topic_id: this.topic.id,
          title: this.topic.title,
          content: this.topic.content,
          accesstoken: this.$store.state.accesstoken,
          tab: "dev"
        })
        .then(function(res) {
          that.$router.back();
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http.get("/topic/" + to.params.id).then(function(res) {
        console.log(res.data);
        vm.topic = res.data.data;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    vm.$http.get("/topic/" + to.params.id).then(function(res) {
      console.log(res.data);
      that.topic = res.data.data;
    });
    next();
  }
};
</script>

<style scoped>
.el-select{
    margin-bottom: 10px;
}
.el-button{
    margin-top: 20px;
    width: 100%;
}
</style>


