<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <el-container>
      <el-header style="padding:0">
        <el-menu
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="topiclist/all">CNode社区PC端</el-menu-item>
          <el-menu-item index="msg">消息中心
            <el-badge :value="badgeText" class="item"></el-badge>
          </el-menu-item>
          <el-menu-item v-show="isLogined" index="create">发表新话题</el-menu-item>
          <el-menu-item v-show="isLogined" index="center">个人中心</el-menu-item>

          <el-menu-item index="login" v-if="isLogined == false">登录</el-menu-item>
          <el-menu-item index="logout" v-else>退出</el-menu-item>
        </el-menu>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
    </el-container>
    <el-backtop :bottom="100">
      <div
        style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
        line-height: 40px;
        color: #1989fa;
      }"
      >
        <i class="el-icon-upload2"></i>
      </div>
    </el-backtop>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "App",
  data() {
    return {
      badgeText:""
    };
  },
  created() {
    //读取localStorage数据
    var loginStatus = window.localStorage.getItem("loginStatus");
    if (loginStatus) {
      //执行用户处理登录状态
      this.mutationLogin();
      //为了再次刷新页面的时候state里面的loginname仍然有值
      this.mutationLoginname(window.localStorage.getItem("loginname"));
      this.mutationAccesstoken(window.localStorage.getItem("accesstoken"));
      this.mutationAuthorId(window.localStorage.getItem("author_id"));
    }
     if(this.isLogined){
       //  执行ajax请求 获取未读消息
      this.getUnreadmMsgCount();
      var that=this;
      window.setInterval(function(){
          that.getUnreadmMsgCount()
      },1000*60*3);
     }
  },
  computed: {
    ...mapState(["topics", "isLogined"]), //辅助函数
  },
  methods: {
    ...mapMutations([
      "mutationLogin",
      "mutationLoginname",
      "mutationAccesstoken",
      "mutationAuthorId"
    ]),
    // 获取服务器端未读消息个数
    getUnreadmMsgCount:function(){
      var that=this;
      this.$http.get("/message/count",{
        params:{
          accesstoken:this.$store.state.accesstoken
        }
      }).then(function(res){
        console.log(res.data.data)
        if(res.data && res.data.success){
          that.badgeText=res.data.data
        }
      })
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      //实现路由跳转
      this.$router.push("/" + keyPath);
    },
  }
};
</script>

<style>
#app {
  background-color: #e1e1e1;
}
</style>



