<template>
  <div>
    <h3>用户登录</h3>
    <el-input placeholder="请输入访问令牌Access Token"  v-model="accesstoken">
      <template slot="prepend">Access Token:</template>
      
    </el-input>
    <div>
        <el-button type="success" style="width:100%" @click.native="dologin">登录</el-button>
    </div>
    <el-alert v-show="isShowToast"
    :title=toastText
    type="error">
  </el-alert>
  </div>
</template>

<script>
import {mapMutations} from 'vuex';
export default {
  name: "login",
  data(){
      return {
        accesstoken:'',
        isShowToast:false,
        toastText:""
      }
    },
    methods:{
      ...mapMutations(["mutationLogin","mutationLoginname","mutationAccesstoken","mutationAuthorId"]),
      // 执行用户登录
      dologin:function(){
        var that=this;
        this.$http.post("/accesstoken",{
          accesstoken:this.accesstoken
        }).then(function(res){
            // console.log(res.data)
            // 验证成功后  登录成功  修改vuex的登录状态isLogined的值
            that.mutationLogin();

            that.mutationLoginname(res.data.loginname);
            that.mutationAccesstoken(that.accesstoken)
            that.mutationAuthorId(res.data.id)


            // vuex中的数据保存在内存中的  当前应用程序生命周期结束是 vuex中的数据就会丢失
            //用户持久化登录  本地存储  localStorage  sessionStorage

            //保存登录后的数据到本地存储当中
            window.localStorage.setItem("loginStatus",true)
            window.localStorage.setItem("loginname",res.data.loginname)
            window.localStorage.setItem("accesstoken",that.accesstoken)

            window.localStorage.setItem("author_id",res.data.id)
            // 实现页面跳转 (个人中心)
            that.$router.push('/user/'+res.data.loginname)
            that.$router.go(0);
        }).catch(function(error){
            // 请求失败时会在catch处理错误
            console.log(error.response)
            that.isShowToast=true
            that.toastText=error.response.data.error_msg;
            that.$notify.error({
                title: '错误',
                message:error.response.data.error_msg
            });
        })
      }
    }
};
</script>

<style scoped>
.el-input{
    margin-top: 50px;
}
.el-button{
    margin-top: 30px;
}
</style>


